.container {
  margin: 0;
  margin-bottom: 30px;
  min-height: 200px;
}

.twoItems {
  width: 380px;
  font-size: 14px;
}

.threeItems {
  width: 260px;
  font-size: 12.5px;
}

.title {
  width: 100%;
  height: 45px;
  text-align: center;
  font-weight: 600;
  font-size: 12px;
  line-height: 42px;
}

.items {
  background: #F3F3F3;
  min-height: 400px;
  margin-bottom: 10px;
  padding: 25px;
  font-weight: 500;
  font-weight: 500;
}

.notes {
  background: #F3F3F3;
  min-height: 120px;
  padding: 25px;
  font-weight: 500;
  font-size: 12.5px;
}

@media screen and (max-width: 825px) {
  .twoItems {
    width: 345px;
    font-size: 14px;
  }

  .threeItems {
    width: 345px;
    font-size: 14px;
  }
}
@media screen and (max-width: 360px) {
  .twoItems {
    width: 305px;
    font-size: 14px;
  }

  .threeItems {
    width: 305px;
    font-size: 14px;
  }
}